import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

// RxJS
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

const conversationUrl = environment.CONVERSATION_BASE_URL;

// models

@Injectable()
export class ConversationsApiService {
  constructor(private http: HttpClient) {}

  public get(url: string): Observable<any> {
    return this.http.get(`${conversationUrl}/${url}`);
  }

  public post(url: string, data: {}): Observable<any> {
    return this.http.post(`${conversationUrl}/${url}`, data);
  }

  public file(url: string): Observable<any> {
    return this.http.get(`${conversationUrl}/${url}`, {
      responseType: 'blob',
      observe: 'body'
    });
  }
}
