import { Injectable } from '@angular/core';

// RxJS
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';

// services
import { ConversationsService } from './conversation-service';
import { ConversationsApiService } from './api-service';

// models
import { Conversation } from '../models/conversation';
import { Note } from '../models/note';

@Injectable()
export class NotesService {
  private _userNotesStream: BehaviorSubject<Note[]> = new BehaviorSubject<Note[]>([]);

  constructor(private conversationService: ConversationsService, private apiService: ConversationsApiService) {
    // refreshes a list of contact notes when current conversation changes
    this.conversationService.currentConversation.subscribe((currentConversation: Conversation) => {
      if (currentConversation === null) {
        this._userNotesStream.next([]);
      } else if (currentConversation.contact != null) {
        this.apiService.get('Contact/Note/' + currentConversation.contact.contactId).subscribe((notes: Note[]) => {
          this._userNotesStream.next(notes);
        });
      }
    });
  }

  public get userNotes(): Observable<Note[]> {
    return this._userNotesStream.asObservable();
  }

  /**
   * Tries to store a new note and puts it in the list of notes if the note was stored
   */
  public async createNote(note: string): Promise<boolean> {
    return this.storeContactNote(note).then((noteId: number) => {
      if (!!noteId) {
        const currentNotes = this._userNotesStream.getValue();
        currentNotes.push({
          noteId: noteId,
          note
        });
      }
      return !!noteId;
    });
  }

  /**
   * Tries to store a new note
   */
  private storeContactNote(note: string): Promise<number> {
    const currentConversation = this.conversationService.currentConversationSnapshot;
    return this.apiService
      .post('Contact/Note', {
        contactId: currentConversation.contact.contactId,
        note
      })
      .pipe(map(({ noteId }: { noteId: number }) => noteId))
      .toPromise();
  }
}
