import { Injectable } from '@angular/core';

// RxJS
import { Observable, of, BehaviorSubject, Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

// services
import { Store, select } from '@ngrx/store';
import { AppState, getAuthUser } from 'src/app/store/reducers';
import { ConversationsApiService } from './api-service';
import { ConversationsRealTimeService } from './conversations-real-time-service';

// models
import { Conversation } from '../models/conversation';
import { ConversationsListFilter } from '../models/conversation-filter';
import { INewConversation, IUser } from '../../../models';

@Injectable()
export class ConversationsService {
  private _filters: ConversationsListFilter[] = [];
  private _conversationsStream: BehaviorSubject<Conversation[]> = new BehaviorSubject<Conversation[]>([]);
  private _currentConversationStream: BehaviorSubject<Conversation> = new BehaviorSubject<Conversation>(null);
  private subscriptions: Subscription[] = [];

  constructor(
    private apiService: ConversationsApiService,
    private realTimeService: ConversationsRealTimeService,
    private store: Store<AppState>
  ) {}

  unsubscribe(): void {
    for (let i = 0; i < this.subscriptions.length; i++) {
      this.subscriptions[i].unsubscribe();
    }
    this.subscriptions = [];
  }

  public get conversations(): Observable<Conversation[]> {
    return this._conversationsStream.asObservable();
  }

  public get conversationsSnapshot(): Conversation[] {
    return this._conversationsStream.getValue();
  }

  public get filters(): Observable<ConversationsListFilter[]> {
    return of(this._filters);
  }

  public get currentConversation(): Observable<Conversation> {
    return this._currentConversationStream.asObservable();
  }

  public get currentConversationSnapshot(): Conversation {
    return this._currentConversationStream.getValue();
  }

  /**
   * Sets or updates the conversations list filter
   * @param name Filter name
   * @param value Filter value
   */
  public setFilter(name: string, value: string | number | string[] | number[]): void {
    const filterIndex = this._filters.findIndex((filter: ConversationsListFilter) => filter.name === name);
    if (filterIndex === -1) {
      this._filters.push({ name: name, value: value });
    } else {
      this._filters[filterIndex]['value'] = value;
    }
    this.loadConversations();
  }

  /**
   * Loads the most recent conversations
   */
  public loadConversations(): void {
    this.unsubscribe();
    const companyId = sessionStorage.getItem('upbookClientCompanyId');
    this.apiService.get('Conversation/' + companyId).subscribe((conversations: Conversation[]) => {
      this._conversationsStream.next(conversations);
    });

    // subscribes for conversations list changes, which include new incoming and outgoing conversations
    this.realTimeService.connect();

    const conversationSubscription = this.realTimeService.conversationsStream.subscribe(data => {
      this.store.pipe(select(getAuthUser)).subscribe((user: IUser) => {
        // add at begin of list
        const conversations: Conversation[] = this.conversationsSnapshot;
        conversations.unshift({
          conversationId: data.conversationId,
          phoneNumber: data.payload.phoneNumber,
          messagePreview: data.payload.messagePreview,
          contact: data.payload.contact,
          lastUpdate: data.payload.lastUpdate,
          answeredBy: data.payload.answeredBy
        });

        // update unread counter
        if (user.id !== data.userId) {
          if (conversations[0].unread == null) {
            conversations[0].unread = 1;
          } else {
            conversations[0].unread++;
          }
        }

        this._conversationsStream.next(conversations);
      });
    });
    this.subscriptions.push(conversationSubscription);

    // subscribes for messages changes to update messagePreview and lastUpdate properties when new message come
    const messageSubscription = this.realTimeService.messagesStream.subscribe(data => {
      const conversations: Conversation[] = this._conversationsStream.getValue();
      const messageConversationIndex = conversations.findIndex((conversation: Conversation) => {
        return conversation.conversationId === data.conversationId;
      });

      if (messageConversationIndex !== -1) {
        // message info
        const conversation = conversations[messageConversationIndex];
        conversation.messagePreview = data.payload.message;
        conversation.lastUpdate = data.payload.created;
        if (data.direction === 'in') {
          conversation.answeredBy = '';
        } else {
          conversation.answeredBy = data.payload.userFullName;
        }
        // update unread counter only if chat is not currently opened
        if (
          this.currentConversationSnapshot == null ||
          this.currentConversationSnapshot.conversationId !== data.conversationId
        ) {
          // update unread counter
          if (conversation.unread == null) {
            conversation.unread = 1;
          } else {
            conversation.unread++;
          }
        }

        // reorder
        this.moveElement(conversations, messageConversationIndex, 0);

        // update
        this._conversationsStream.next(conversations);
      }
    });
    this.subscriptions.push(messageSubscription);
  }

  public async chatAvailable(): Promise<any> {
    const companyId = sessionStorage.getItem('upbookClientCompanyId');
    if (!companyId) {
      return Promise.resolve({ success: false });
    }
    return this.apiService.get('Conversation/Available/' + companyId).toPromise();
  }

  /**
   * Refreshes the current conversation property
   * @param conversation Chosen conversation
   */
  public setCurrentConversation(conversation: Conversation): void {
    this._currentConversationStream.next(null);
    this._currentConversationStream.next(conversation);
  }

  /**
   * Tries to create a conversation
   */
  public startConversation(newConversation: INewConversation): Promise<number> {
    const companyId = sessionStorage.getItem('upbookClientCompanyId');
    return this.apiService
      .post('Conversation/' + companyId, newConversation)
      .pipe(map(({ conversationId }: { conversationId: number }) => conversationId))
      .toPromise();
  }

  /**
   * Zero the "unread" property of a conversation which ID is equal to the ID of the passed conversation
   * Refreshes the list of conversations
   */
  public zeroUnreadMessages(conversationToUpdate: Conversation): void {
    const conversations = this.conversationsSnapshot;
    const conversationToUpdateIndex = conversations.findIndex((conversation: Conversation) => {
      return conversation.conversationId === conversationToUpdate.conversationId;
    });
    if (conversationToUpdateIndex !== -1) {
      conversations[conversationToUpdateIndex].unread = 0;
    }
    this._conversationsStream.next(conversations);
  }

  /**
   * Move elemtn in array (help function)
   */
  private moveElement(arr: any, oldIndex: number, newIndex: number): any {
    if (newIndex >= arr.length) {
      let k = newIndex - arr.length + 1;
      while (k--) {
        arr.push(undefined);
      }
    }
    arr.splice(newIndex, 0, arr.splice(oldIndex, 1)[0]);
    return arr; // for testing
  }
}
