import { Component, Output } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

// Bootstrap
import { BsModalRef } from 'ngx-bootstrap/modal';

// models
import { ConversationsService } from 'src/app/conversations/shared/services/conversation-service';
import { ValidationService } from 'src/app/shared/validators/validators.service';

@Component({
  templateUrl: 'new-conversation-modal.component.html',
  styleUrls: ['new-conversation-modal.component.scss']
})
export class NewConversationModalComponent {
  @Output()
  public isPending: boolean = false;
  public errorMessage: string = undefined;
  public newConversationForm: FormGroup = new FormGroup({
    phoneNumber: new FormControl('', [Validators.required]),
    message: new FormControl('', [Validators.required, ValidationService.noWhitespaceValidator])
  });

  constructor(public bsModalRef: BsModalRef, private conversationsService: ConversationsService) {}

  /**
   * Checks the form for validity, return new conversation values, and closes the modal
   */
  public send(): void {
    if (this.newConversationForm.invalid || this.isPending) {
      return;
    }

    this.errorMessage = undefined;
    this.isPending = true;
    this.conversationsService
      .startConversation(this.newConversationForm.value)
      .then(() => {
        this.isPending = false;
        this.bsModalRef.hide();
      })
      .catch(error => {
        this.errorMessage = error.error.message;
        this.isPending = false;
      });
  }

  /**
   * Closes the modal
   */
  public close(): void {
    this.bsModalRef.hide();
  }

  /**
   * Shows whether is attribute invalid
   * @param attribute
   */
  public isInvalid(attribute: string): boolean {
    return this.newConversationForm.controls[attribute].invalid && this.newConversationForm.controls[attribute].touched;
  }
}
