import { Injectable } from '@angular/core';

// RxJS
import { BehaviorSubject, Observable, Subscription } from 'rxjs';
import { filter, map, tap } from 'rxjs/operators';

// services
import { ConversationsApiService } from './api-service';
import { ConversationsService } from './conversation-service';
import { ConversationsRealTimeService } from './conversations-real-time-service';

// models
import { ConversationMessage } from '../models/conversation-message';
import { Conversation } from '../models/conversation';

const MESSAGES_PER_PAGE = 10;

@Injectable()
export class MessagesService {
  private _conversationMessagesStream: BehaviorSubject<ConversationMessage[]>;
  private page: number = 0;
  private allMessagesLoaded: boolean = false;
  private subscriptions: Subscription[] = [];

  constructor(
    private apiService: ConversationsApiService,
    private conversationService: ConversationsService,
    private realTimeService: ConversationsRealTimeService
  ) { }
  unsubscribe(): void {
    for (let i = 0; i < this.subscriptions.length; i++) {
      this.subscriptions[i].unsubscribe();
    }
    this.subscriptions = [];
  }

  public LoadMessages(): void {
    this.unsubscribe();
    this._conversationMessagesStream = new BehaviorSubject<ConversationMessage[]>([]);
    // refreshes a list of messages and related properties when current conversation changes
    const changeConversationSubscription = this.conversationService.currentConversation
      .subscribe((currentConversation: Conversation) => {
        if (currentConversation === null) {
          this._conversationMessagesStream.next([]);
          this.page = 0;
          this.allMessagesLoaded = false;
        } else {
          this.apiService
            .get('Message/' + currentConversation.conversationId + '?page=' + this.page)
            .subscribe((conversationMessages: ConversationMessage[]) => {
              if (conversationMessages.length < MESSAGES_PER_PAGE) {
                this.allMessagesLoaded = true;
              }
              this._conversationMessagesStream.next(conversationMessages);
            });
        }
      });
    this.subscriptions.push(changeConversationSubscription);
    // subscribes to new messages from sockets
    // loads only those ones which are associated to the current conversation
    // marks new messages as read
    const messagesSubscription = this.realTimeService.messagesStream
      .pipe(
        filter(data => {
          const currentConversation = this.conversationService.currentConversationSnapshot;
          return currentConversation !== null && currentConversation.conversationId === data.conversationId;
        })
      )
      .subscribe(data => {
        const messages = this._conversationMessagesStream.getValue();
        messages.push({
          direction: data.direction,
          message: data.payload.message,
          userFullName: data.payload.userFullName,
          messageId: data.payload.messageId,
          created: data.payload.created
        });
        this._conversationMessagesStream.next(messages);
        this.markMessageAsRead(data.payload.messageId);
      });
    this.subscriptions.push(messagesSubscription);
  }

  /**
   * Returns an observable with messages sorted from the latest to the newest
   */
  public get currentConversationMessages(): Observable<ConversationMessage[]> {
    return this._conversationMessagesStream.asObservable().pipe(
      tap((messages: ConversationMessage[]) => {
        messages.sort((first, second) => {
          return first.messageId < second.messageId ? -1 : 1;
        });
      })
    );
  }

  public get currentConversationMessagesSnapshot(): ConversationMessage[] {
    return this._conversationMessagesStream.getValue();
  }

  /**
   * Loads messages that were sent earlier that the last one from the current chat and adds them to the chat log
   */
  public loadPreviousMessages(): void {
    if (!this.allMessagesLoaded) {
      const currentConversation = this.conversationService.currentConversationSnapshot;
      this.page++;
      this.apiService
        .get('Message/' + currentConversation.conversationId + '?page=' + this.page)
        .subscribe((previousMessages: ConversationMessage[]) => {
          if (previousMessages.length < MESSAGES_PER_PAGE) {
            this.allMessagesLoaded = true;
          }
          const currentMessages = this.currentConversationMessagesSnapshot;
          this._conversationMessagesStream.next(previousMessages.concat(currentMessages));
        });
    }
  }

  /**
   * Sends a new message to the server
   */
  public sendMessage(message: string): Promise<boolean> {
    return this.apiService
      .post('Message', {
        conversationId: this.conversationService.currentConversationSnapshot.conversationId,
        message: message
      })
      .pipe(map(({ messageId }: { messageId: number }) => !!messageId))
      .toPromise();
  }

  /**
   * Sends a request to mark a message and previous ones as read
   */
  public markMessageAsRead(messageId: number): Promise<boolean> {
    return this.apiService
      .post('Message/Read', {
        messageId: messageId
      })
      .pipe(map(({ success }: { success: boolean }) => success))
      .toPromise();
  }
}
