import { Injectable, Renderer2, RendererFactory2 } from '@angular/core';

// RxJS
import { BehaviorSubject, Observable } from 'rxjs';

// services
import { ConversationsService } from './conversation-service';
import { ConversationsApiService } from './api-service';

// models
import { Attachment } from '../models/attachment';
import { Conversation } from '../models/conversation';

@Injectable()
export class AttachmentsService {
  private _userAttachmentsStream: BehaviorSubject<Attachment[]> = new BehaviorSubject<Attachment[]>([]);
  private renderer: Renderer2;

  constructor(
    private conversationService: ConversationsService,
    private apiService: ConversationsApiService,
    rendererFactory: RendererFactory2
  ) {
    this.renderer = rendererFactory.createRenderer(null, null);
    this.conversationService.currentConversation.subscribe((currentConversation: Conversation) => {
      if (currentConversation === null) {
        this._userAttachmentsStream.next([]);
      } else if (currentConversation.contact != null) {
        this.apiService
          .get('Contact/Attachment/' + currentConversation.contact.contactId)
          .subscribe((attachments: Attachment[]) => {
            this._userAttachmentsStream.next(attachments);
          });
      }
    });
  }

  public get userAttachments(): Observable<Attachment[]> {
    return this._userAttachmentsStream.asObservable();
  }

  /**
   * Tries to store uploaded file and put it in the list of current conversation attachments
   */
  public addAttachment(formData: FormData): Promise<boolean> {
    return this.storeAttachment(formData).then((uploadedAttachment: Attachment) => {
      const currentAttachments = this._userAttachmentsStream.getValue();
      currentAttachments.push(uploadedAttachment);
      this._userAttachmentsStream.next(currentAttachments);
      return true;
    });
  }

  /**
   * Tries to store an uploaded file
   */
  private storeAttachment(formData: FormData): Promise<Attachment> {
    const currentConversation = this.conversationService.currentConversationSnapshot;
    const contactId = currentConversation.contact.contactId;
    return this.apiService.post('Contact/Attachment/' + contactId, formData).toPromise();
  }

  /**
   * Loads a file from the server and sends it to client's device
   * @param attachment Attachment that should be loaded
   */
  public downloadAttachment(attachment: Attachment): void {
    this.apiService
      .file('Contact/Attachment/Download/' + attachment.attachmentId)
      .toPromise()
      .then(res => {
        const url = window.URL.createObjectURL(res);
        const a = this.renderer.createElement('a');
        this.renderer.appendChild(document.body, a);
        this.renderer.setStyle(a, 'display', 'none');
        this.renderer.setAttribute(a, 'href', url);
        this.renderer.setAttribute(a, 'download', attachment.fileName);
        a.click();
        window.URL.revokeObjectURL(url);
        this.renderer.removeChild(document.body, a);
      });
  }
}
