import { Injectable } from '@angular/core';

// RxJS
import { Observable } from 'rxjs';
import { RxWebsocketSubject } from '../components/rxWebsocketSubject';
import { filter } from 'rxjs/operators';

// services
// import { TokenService } from 'src/app/api/token/token.service';
// import { environment } from 'src/environments/environment';

// const socketUrl = environment.CONVERSATION_SOCKET_URL;

@Injectable()
export class ConversationsRealTimeService {
  private _eventsStream: RxWebsocketSubject<any>;

  // constructor(private tokenService: TokenService) {
  //
  // }

  public connect(): void {
    // const companyId = sessionStorage.getItem('upbookClientCompanyId');
    // this._eventsStream = new RxWebsocketSubject(socketUrl + `?companyId=${companyId}`, this.tokenService.getToken());
  }

  /**
   * Returns an Observable that emits when new message comes which is related to conversation message
   */
  public get messagesStream(): Observable<any> {
    return this._eventsStream.pipe(filter((message: any) => message.type != null && message.type === 'message'));
  }

  /**
   * Returns an Observable that emits when new message comes which is related to conversations
   */
  public get conversationsStream(): Observable<any> {
    return this._eventsStream.pipe(filter((message: any) => message.type != null && message.type === 'conversation'));
  }
}
